import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { changeonsumptionType } from '../../../reducers/payment-reducer';
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';

const ConsumptionTypes = () => {

    const checkedonsumptionType = useSelector((state) => state.payment.checkedonsumptionType);
    const consumptionTypes       = useSelector((state) => state.payment.consumptionTypes);
    const dispatch               = useDispatch();

    const onChangeConsumptionType = (consumptionType) => dispatch(changeonsumptionType(consumptionType));

    return (
        <ToggleButtonGroup type="radio" name="income-type" value={checkedonsumptionType} onChange={onChangeConsumptionType}>
            {consumptionTypes.map((consumptionType, id) => (
                <ToggleButton
                    key={id}
                    id={`income-type-${id}`}
                    type="radio"
                    variant={checkedonsumptionType === consumptionType.value ? 'primary' : 'outline-primary'}
                    value={consumptionType.value}
                    checked={checkedonsumptionType === consumptionType.value}
                >
                    {consumptionType.title}
                </ToggleButton>
            ))}
        </ToggleButtonGroup>
    );
}

export default ConsumptionTypes;
